<template>
  <el-dialog
    title="合同核验"
    :visible.sync="visible"
    :close-on-click-modal="false"
    @close="handleClose"
  >
    <div class="box" v-loading="loading">
      <div class="file-box">
        <div>
          <el-upload
            ref="upload"
            v-show="!file.fid"
            class="upload"
            :action="uploadUrl"
            :limit="1"
            :multiple="false"
            drag
            list-type="picture-card"
            :headers="headers"
            accept="doc, docx"
            :on-success="handleUpload"
            :before-upload="handleBeforeUpload"
          >
            <i class="el-icon-plus"></i>
          </el-upload>
          <div class="preview" v-show="file.fid">
            <img class="upload-thumbnail" :src="file.previewUrl" />
            <span class="btn zoom" @click="handlePreview">
              <i class="el-icon-zoom-in"></i>
            </span>
            <span class="btn remove" @click="handleRemove">
              <i class="el-icon-delete"></i>
            </span>
          </div>
        </div>
        <div class="file-name" v-show="!!file.fid">{{file.name}}</div>
        <div class="file-tip" v-show="!file.fid">选择上传一份合同，立刻核验签名是否有效，内容是否被篡改</div>
      </div>
      <div class="result-box" v-show="!!file.fid">
        <p><span class="title">签名信息</span>有效 <i style="color:#67C23A" class="el-icon-success"></i> </p>
        <p><span class="title">文档内容</span>无篡改 <i style="color:#67C23A" class="el-icon-success"></i></p>
        <p><span class="title">负责人</span>许X</p>
        <p><span class="title">签署日期</span>2020年5月25日</p>
        <p><span class="title">版本</span>5ecaa19621298a732bfae6b4</p>
        <p><span class="title">所属项目</span>仓库设备租赁</p>
      </div>
    </div>
    <el-dialog :visible.sync="dialogVisible" append-to-body>
      <div class="box">
        <img class="preview-page" :src="file.previewUrl" />
      </div>
    </el-dialog>
  </el-dialog>
</template>

<script>
import { uploadTempContract } from '@/action/contract'
import { filePreview } from '@/action/open'
export default {
  props: ['open'],
  data() {
    return {
      uploadUrl: uploadTempContract(),
      headers: {
        Authorization: this.$store.getters.token
      },
      loading: false,
      visible: this.open,
      dialogVisible: false,
      disabled: false,
      file: {
        fid: '',
        uri: '',
        name: '',
        previewUrl: '',
        origin: {}
      }
    }
  },
  watch: {
    open(newV, oldV) {
      this.visible = newV
    }
  },
  methods: {
    handleBeforeUpload() {
      this.loading = true
    },
    async handleUpload(file) {
      let { name, type } = file.baseInfo
      this.file = {
        fid: file.cid,
        uri: file.uri,
        name: name + '.' + type,
        previewUrl: '',
        origin: file
      }
      this.file.previewUrl = await filePreview(file.cid, name, type, file.uri)
      this.loading = false
    },
    handleRemove() {
      this.$refs.upload.clearFiles()
      this.$nextTick(_=>{
        this.clear()
      })
    },
    handlePreview() {
      this.dialogVisible = true
    },
    handleClose() {
      this.clear()
      this.$emit('close')
    },
    clear() {
      this.file = {
        fid: '',
        uri: '',
        name: '',
        previewUrl: '',
        origin: {}
      }
    }
  }
}
</script>

<style scoped>
.box {
  height: 60vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.file-box {
  width: 270px;
  text-align: center;
}

.file-tip {
  font-size: 12px;
  padding: 30px;
  color: #909399;
}

.upload >>> .el-upload {
  width: 180px;
  height: 260px;
  line-height: 280px;
}

.upload >>> .el-upload-dragger {
  width: 180px;
  height: 260px;
  line-height: 280px;
  z-index: 999;
}

.preview {
  position: relative;
  width: 180px;
  height: 260px;
  line-height: 280px;
  margin: auto;
}

.preview > .btn {
  display: none;
}

.preview:hover > .btn {
  display: block;
}

.preview > img {
  position: absolute;
  left: 0;
  height: 100%;
  width: 100%;
}

.preview > .btn {
  position: absolute;
  width: 50%;
  height: 100%;
  font-size: 24px;
  color: white;
  font-weight: 900;
  background-color: rgba(0, 0, 0, 0.4);
  cursor: pointer;
}

.preview .zoom {
  left: 0;
}

.preview .remove {
  left: 50%;
}

.preview-page {
  height: 100%;
  margin: auto;
}

.file-name {
  padding: 30px;
}

.result-box {
  flex: 1;
  padding-bottom: 50px;
}

.result-box  > p {
  color: #757575;
  margin: 30px;
}

.title {
  display: inline-block;
  height: 100%;
  width: 120px;
  color:#303133
}
</style>



